.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.handDiv {
  display: flex;
  justify-content: center;
  width: 100%;
}

.divItem3 {
  padding: 20px 20px;
  margin: 30px;
  text-align: center;
  background-color: transparent;
  border: none;
  cursor: pointer;

}

.hand {
  width:20vw;
  height:20vw;
}

button:hover{
  background-color: #fe8d3d;
  border-radius: 50%;
}